import { mapActions } from 'vuex';
export default {
  props: ['asset', 'chain', 'address', 'btnClass', 'screen'],
  methods: {
    ...mapActions('app', ['setBuyCryptoModalOpen']),
    open() {
      const {
        asset,
        chain,
        address,
        screen
      } = this;
      this.setBuyCryptoModalOpen({
        open: true,
        asset,
        chain,
        address,
        screen
      });
    }
  }
};